<template>
  <div class="container">
   <div class="name" v-show="entry_name">
     <label class="prompt_label"> {{prompt0}} </label>
    <input type="text" class="text" v-model="input_name" @keyup="key_monitor_name">
    <label  class="enter"> Hit <b>enter</b> to save </label>
   </div>
   <div class="entry-2" v-show="entry_enquiry_type">
     <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}}  </label> <br/>
     <button class="btn" @click="btn_answer1_enquiry" > {{answer1_enquiry_type}} </button>
     <button class="btn" @click="btn_answer2_enquiry"> {{answer2_enquiry_type}} </button>
   </div>
   <div class="entry-3" v-show="entry_project_type">
    <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}} {{answer1_enquiry_type}} {{prompt2}} </label> <br/>
    <button class="btn" @click="btn_answers_project_type(answer1_project_type)" > {{answer1_project_type}} </button>
    <button class="btn" @click="btn_answers_project_type(answer2_project_type)"> {{answer2_project_type}} </button>
   </div>
   <div class="entry-5" v-show="entry_project_desc1">
    <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}} {{answer1_enquiry_type}} {{prompt2}} {{answers_project_type}} {{prompt3}} </label> <br/>
    <input type="text" class="text" v-model="project_desc1" @keyup="key_monitor_desc1">
    <label  class="enter"> Hit <b>enter</b> to save </label>
   </div>
   <div class="entry-6" v-show="entry_time_horizon">
    <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}} {{answer1_enquiry_type}} {{prompt2}} {{answers_project_type}} {{prompt3}} {{project_desc1}}. <br/> {{prompt4}} </label> <br/>
    <button class="btn" @click="btn_answers_time_horizon(answer1_time_horizon)"> {{answer1_time_horizon}} </button>
    <button class="btn" @click="btn_answers_time_horizon(answer2_time_horizon)"> {{answer2_time_horizon}} </button>
    <button class="btn" @click="btn_answers_time_horizon(answer3_time_horizon)"> {{answer3_time_horizon}} </button>
    <button class="btn" @click="btn_answers_time_horizon(answer4_time_horizon)"> {{answer4_time_horizon}} </button>
   </div>
   <div class="entry-7" v-show="entry_help_needed">
    <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}} {{answer1_enquiry_type}} {{prompt2}} {{answers_project_type}} {{prompt3}} {{project_desc1}}. <br/> {{prompt4}} {{answers_time_horizon}} {{prompt5}} </label> <br/>
    <button class="btn" @click="btn_answers_help_needed(answer1_help_needed)"> {{answer1_help_needed}} </button>
    <button class="btn" @click="btn_answers_help_needed(answer2_help_needed)"> {{answer2_help_needed}} </button>
    <button class="btn" @click="btn_answers_help_needed(answer3_help_needed)"> {{answer3_help_needed}} </button>
    <button class="btn" @click="btn_answers_help_needed(answer4_help_needed)"> {{answer4_help_needed}} </button>
   </div>
   <div class="entry-8" v-show="entry_email_project">
    <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}} {{answer1_enquiry_type}} {{prompt2}} {{answers_project_type}} {{prompt3}} {{project_desc1}}. <br/> {{prompt4}} {{answers_time_horizon}} {{prompt5}} {{answers_help_needed}}. <br/> {{prompt7}} </label> <br/>
    <input type="text" class="text" v-model="email" @keyup="key_monitor_email_project">
    <label class="enter"> Hit <b>enter</b> to save </label>
   </div>
   <div class="entry-4" v-show="entry_project_desc2">
    <label class="prompt_label">  {{prompt0}} {{input_name}} {{prompt1}} {{answer2_enquiry_type}}. <br/> {{prompt6}} <br/> </label>
    <input type="text" class="text" v-model="project_desc2" @keyup="key_monitor_desc2">
    <label class="enter"> Hit <b>enter</b> to save </label>
   </div>
   <div class="email" v-show="entry_email_question">
     <label class="prompt_label"> {{prompt0}} {{input_name}} {{prompt1}} {{answer2_enquiry_type}}. <br/> {{prompt6}} {{project_desc2}}. <br/> {{prompt7}}  </label> <br/>
       <input type="text" class="text" v-model="email" @keyup="key_monitor_email_question">
       <label class="enter"> Hit <b>enter</b> to save </label>
   </div>
   <div class="" v-show="entry_project">
    <label class="prompt_label"> {{prompt0}} <span class="span_text" @click="edit_name()"> {{input_name}}</span>  <span v-if="switchName"> <input type="text" class="text" v-model="edit_input_name" @keyup="key_monitor_edit_name" ></span>
      {{prompt1}} <span class="span_text" @click="edit_answer_enquiry"> {{answer1_enquiry_type}}</span> <br/>
      {{prompt2}} <span class="span_text" @click="edit_project_type">{{answers_project_type}} </span> 
      {{prompt3}} <span class="span_text" @click="edit_project_desc1">{{project_desc1}} </span> 
       <input v-if="switchProjectDesc1" type="text" class="text" v-model=" edit_input_project_desc1" @keyup="key_monitor_edit_desc1"> <br/>
      {{prompt4}} <span class="span_text" @click="edit_answers_time_horizon "> {{answers_time_horizon}}. </span> <br/>
      {{prompt5}} <span class="span_text" @click="edit_answers_help_needed"> {{answers_help_needed}} </span> <br/>
      {{prompt7}} <span class="span_text" @click="edit_entry_email_project"> {{email}} </span> <br/>
      <input v-if="switchEmailProject" type="text" class="text" v-model="edit_email" @keyup="key_monitor_edit_email_project">
      <button v-if="switchAnswerEnquiry" class="btn" @click="edit_btn_answer1_enquiry2(answer1_enquiry_type)"> {{answer1_enquiry_type}} </button> 
      <button v-if="switchAnswerEnquiry" class="btn" @click="edit_btn_answer2_enquiry2(answer2_enquiry_type)"> {{answer2_enquiry_type}} </button> 
      <button v-if="switchProjectType" class="btn" @click="edit_btn_answers_project_type(answer1_project_type)" > {{answer1_project_type}} </button>
      <button v-if="switchProjectType" class="btn" @click="edit_btn_answers_project_type(answer2_project_type)"> {{answer2_project_type}} </button>
      <button v-if="switchTimeHorizon" class="btn" @click="edit_btn_answers_time_horizon(answer1_time_horizon)"> {{answer1_time_horizon}} </button>
      <button v-if="switchTimeHorizon" class="btn" @click="edit_btn_answers_time_horizon(answer2_time_horizon)"> {{answer2_time_horizon}} </button>
      <button v-if="switchTimeHorizon" class="btn" @click="edit_btn_answers_time_horizon(answer3_time_horizon)"> {{answer3_time_horizon}} </button>
      <button v-if="switchTimeHorizon" class="btn" @click="edit_btn_answers_time_horizon(answer4_time_horizon)"> {{answer4_time_horizon}} </button>
      <button v-if="switchHelpNeeded" class="btn" @click="edit_btn_answers_help_needed(answer1_help_needed)"> {{answer1_help_needed}} </button>
      <button v-if="switchHelpNeeded" class="btn" @click="edit_btn_answers_help_needed(answer2_help_needed)"> {{answer2_help_needed}} </button>
      <button v-if="switchHelpNeeded" class="btn" @click="edit_btn_answers_help_needed(answer3_help_needed)"> {{answer3_help_needed}} </button>
      <button v-if="switchHelpNeeded" class="btn" @click="edit_btn_answers_help_needed(answer4_help_needed)"> {{answer4_help_needed}} </button>
    </label> 
   </div>
     <div class="" v-show="entry_question">           
     <label class="prompt_label"> {{prompt0}} <span class="span_text" @click="edit_name()"> {{input_name}}</span>  <span v-if="switchName"> <input type="text" class="text" v-model="edit_input_name" @keyup="key_monitor_edit_name" ></span>
      {{prompt1}} <span class="span_text" @click="edit_answer_enquiry()"> {{answer2_enquiry_type}}</span> <br/>
      {{prompt6}} <span class="span_text" @click="edit_project_desc2"> {{project_desc2}}</span>
       <input type="text" v-if="switchProjectDesc2" class="text" v-model="edit_input_project_desc2" @keyup="key_monitor_edit_desc2"><br/>
       {{prompt7}} <span class="span_text" @click="edit_entry_email_question"> {{email}}</span> <br/> 
       <input v-if="switchEmailQuestion" type="text" class="text" v-model="edit_email" @keyup="key_monitor_edit_email_question">
      <button v-if="switchAnswerEnquiry" class="btn" @click="edit_btn_answer1_enquiry(answer1_enquiry_type)" > {{answer1_enquiry_type}} </button>
      <button v-if="switchAnswerEnquiry" class="btn" @click="edit_btn_answer2_enquiry(answer2_enquiry_type)"> {{answer2_enquiry_type}} </button> 
     </label>
   </div>
  </div>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
Vue.use(VueAxios, axios);


export default {
  data() {
    return {
      input_name:'',
      email:'',
      edit_input_name:'',
      edit_input_project_desc1:'',
      edit_input_project_desc2:'',
      edit_email:'',
      project_desc1:'',
      project_desc2:'',
      prompt0:[],
      prompt1:[],
      prompt2:[],
      prompt3:[],
      prompt4:[],
      prompt5:[],
      prompt6:[],
      prompt7:[],
      entry_name: true,
      entry_enquiry_type: false,
      entry_project_type: false,
      entry_project_desc2: false,
      entry_project_desc1: false,
      entry_time_horizon: false,
      entry_help_needed: false,
      entry_email_project: false,
      entry_email_question: false,
      entry_question: false,
      entry_project: false,
      answers_time_horizon:[],
      answer1_enquiry_type:[],
      answer2_enquiry_type:[],
      answers_project_type:[],
      answer1_project_type:[],
      answer2_project_type:[],
      answer1_time_horizon:[],
      answer2_time_horizon:[],
      answer3_time_horizon:[],
      answer4_time_horizon:[],
      answers_help_needed:[],
      answer1_help_needed:[],
      answer2_help_needed:[],
      answer3_help_needed:[],
      answer4_help_needed:[],
      sample:[],
      switchName: false,
      switchAnswerEnquiry: false,
      switchProjectDesc2: false,
      switchEmailQuestion: false,
      switchEmailProject: false,
      switchProjectType: false,
      switchProjectDesc1: false,
      switchTimeHorizon: false,
      switchHelpNeeded: false,
    };
  },
  created() {
    this.GET_JSON();
  },
  methods: {
    key_monitor_name(event) {
       if(event.key == "Enter")
       {
         //eto yung name (sample) nideclare ko nman sa isang variable
        this.sample = this.input_name;
        this.entry_name = false;
        this.entry_enquiry_type = true;
       }
    },
    key_monitor_desc1(event) {
      if(event.key == "Enter")
       {
        // console.log(this.project_desc1);
        this.entry_project_desc1 = false;
        this.entry_time_horizon = true;
       }
    },
    key_monitor_desc2(event) {
      if(event.key == "Enter")
       {
        // console.log(this.project_desc2);
        this.entry_project_desc2 = false;
        this.entry_email_question = true;
       }
    },
    key_monitor_email_question(event) {
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.entry_email_question = false;
        this.entry_question = true;
       }
    },
    key_monitor_email_project(event) {
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.entry_email_project = false;
        this.entry_project = true;
       }
    },
    key_monitor_edit_name(event) {
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.input_name = this.edit_input_name;
        this.switchName = false;
       }
    },
    key_monitor_edit_desc2(event) {
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.project_desc2 = this.edit_input_project_desc2;
        this.switchProjectDesc2 = false;
       }
    },
    key_monitor_edit_email_question(){
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.email = this.edit_email;
        this.switchEmailQuestion = false;
       }
    },
    key_monitor_edit_email_project(){
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.email = this.edit_email;
        this.switchEmailProject = false;
       }
    },
   key_monitor_edit_desc1(event) {
       if(event.key == "Enter")
       {
        // console.log(this.email);
        this.project_desc1 = this.edit_input_project_desc1;
        this.switchProjectDesc1 = false;
       }
    },
    GET_JSON() {
      axios.get('http://35.199.169.44:8080/https://apsel.io/challenge/conversation2.json').then((response) => {
        var api_Data = response.data;
        this.prompt0 = api_Data.questions[0].prompt;
        this.prompt1 = api_Data.questions[1].prompt;
        this.prompt2 = api_Data.questions[2].prompt;
        this.prompt3 = api_Data.questions[3].prompt;
        this.prompt4 = api_Data.questions[4].prompt;
        this.prompt5 = api_Data.questions[5].prompt;
        this.prompt6 = api_Data.questions[6].prompt;
        this.prompt7 = api_Data.questions[7].prompt;
        this.answer1_enquiry_type = api_Data.questions[1].answers[0];
        this.answer2_enquiry_type = api_Data.questions[1].answers[1];
        
        this.answer1_project_type = api_Data.questions[2].answers[0];
        this.answer2_project_type = api_Data.questions[2].answers[1];

        this.answer1_time_horizon = api_Data.questions[4].answers[0];
        this.answer2_time_horizon = api_Data.questions[4].answers[1];
        this.answer3_time_horizon = api_Data.questions[4].answers[2];
        this.answer4_time_horizon = api_Data.questions[4].answers[3];

        this.answer1_help_needed = api_Data.questions[5].answers[0];
        this.answer2_help_needed = api_Data.questions[5].answers[1];
        this.answer3_help_needed = api_Data.questions[5].answers[2];
        this.answer4_help_needed = api_Data.questions[5].answers[3];
      })
    },
    btn_answer1_enquiry() {
        this.entry_enquiry_type = false;
        this.entry_project_type = true;
    },
    btn_answer2_enquiry() {
      this.entry_enquiry_type = false;
      this.entry_project_desc2 = true;
    },
    btn_answers_project_type(choice) {
      this.answers_project_type = choice;
      this.entry_project_type = false;
      this.entry_project_desc1 = true;
    },
    btn_answers_time_horizon(choice) {
      this.answers_time_horizon = choice;
      // console.log(this.answers_time_horizon);
      this.entry_time_horizon = false;
      this.entry_help_needed = true;
   
    },
    btn_answers_help_needed(choice) {
      this.answers_help_needed = choice;
      // console.log(this.answers_help_needed);
      this.entry_help_needed = false;
      this.entry_email_project = true;
    },
    edit_name(){
      this.switchName = true;
      this.input_name = '';
    },
    edit_answer_enquiry() {
      this.switchAnswerEnquiry = true;
    },
    edit_btn_answer1_enquiry(choice) {
      this.edit_btn_answer1_enquiry = choice;
      this.switchAnswerEnquiry = false;
      this.entry_question = false;
      this.entry_project_type = true;
    },
    edit_btn_answer2_enquiry(choice) {
      this.answer2_enquiry_type = choice;
      this.switchAnswerEnquiry = false;
      this.edit_btn_answer1_enquiry =false;
    },
    edit_btn_answer1_enquiry2(choice) {
      this.answer1_enquiry_type = choice;
      this.switchAnswerEnquiry = false;
    },
    edit_btn_answer2_enquiry2(choice) {
      this.answer2_enquiry_type = choice;
      this.switchAnswerEnquiry = false;
      this.entry_project_desc2 = true;
      this.entry_project = false;
    },
    edit_project_desc2() {
      this.switchProjectDesc2 = true;
      this.project_desc2='';
    },
    edit_entry_email_question() {
      this.switchEmailQuestion = true;
      this.email='';
    },
    edit_project_type() {
      this.switchProjectType = true;
    },
    edit_btn_answers_project_type(choice) {
      this.answers_project_type = choice;
      this.switchProjectType = false;
    },
    edit_project_desc1() {
      this.switchProjectDesc1 = true;
      this.project_desc1 = '';
    },
    edit_answers_time_horizon() {
      this.switchTimeHorizon = true;
    },
    edit_btn_answers_time_horizon(choice) {
      this.answers_time_horizon = choice;
      this.switchTimeHorizon = false;
    },
    edit_answers_help_needed() {
      this.switchHelpNeeded = true;
    },
    edit_btn_answers_help_needed(choice) {
      this.answers_help_needed = choice;
      this.switchHelpNeeded = false;
    },
    edit_entry_email_project() {
      this.switchEmailProject = true;
      this.email = '';
    }

  }
}
</script>
